.checkbox{padding:.75rem 1rem .75rem 3rem;display:flex;width:fit-content;align-items:center;background:#f9f9f9;border:1px solid #d2d2d4;border-radius:12px;position:relative}.checkbox__field{-webkit-appearance:none;appearance:none;margin:0;width:20px;height:20px;background-color:#fff;border:2px solid #d2d2d4;border-radius:3px;outline:none;cursor:pointer;position:absolute;top:50%;left:1rem;z-index:3;transform:translateY(-50%)}.checkbox__field:hover{box-shadow:0 0 5px #6a983c}.checkbox__field:checked{background-color:#6a983c;border:2px solid #46760a}.checkbox__field:checked::before{content:"✔";font-size:.9rem;color:#fff;position:absolute;right:2px;top:-3px}.checkbox__label{font-size:.875rem;line-height:1.185rem}.checkbox__label a{color:#151515;transition:color .1s linear}.checkbox__label a:hover{color:#6a983c}.checkbox__label--required::after{content:"*";margin-left:5px;font-size:1rem;color:#e6704b}.checkbox__error{position:absolute;top:100%;left:0;z-index:1;font-weight:400;font-size:.75rem;line-height:1.125rem;color:#e6704b}@media(max-width: 575.98px){.checkbox{padding:.5rem .75rem .5rem 2.5rem}.checkbox__field{left:.75rem}.checkbox__label{font-size:.7rem;line-height:1rem}}